<template>
  <div class="pageContent">
    <el-card shadow="always">
      <el-form :model="form" ref="ruleForm" label-width="180px" class="demo-ruleForm">
        <el-form-item label="活动名称：">
          <div>{{form.name}}</div>
        </el-form-item>
        <el-form-item label="活动商品：" v-if="commodityList.length > 0">
          <!-- 统一商品规格 -->
          <div v-if="commodityList[0].skuType == 0">
            <el-table :data="commodityList" tooltip-effect="dark" style="width: 100%">
              <el-table-column label="商品名称" prop="productName"></el-table-column>
              <el-table-column label="售价（元）" width="140">
                <template slot-scope="scope">{{ scope.row.list[0].price ? '￥'+scope.row.list[0].price : '-' }}</template>
              </el-table-column>
              <el-table-column label="成本价（元）" width="140">
                <template slot-scope="scope">{{ scope.row.list[0].costPrice ? '￥'+scope.row.list[0].costPrice : '-' }}</template>
              </el-table-column>
              <el-table-column width="180">
                <template slot="header" slot-scope="scope">
                  <span :data-scope="scope">砍价底价（元）</span>
                  <el-tooltip class="item" effect="dark" content="售价 > 底价 >= 0" placement="top">
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                </template>
                <template slot-scope="scope">￥{{scope.row.list[0].finallyPrice}}</template>
              </el-table-column>
              <el-table-column label="库存" width="140">
                <template slot-scope="scope">{{scope.row.list[0].skuStock}}</template>
              </el-table-column>
              <el-table-column label="活动库存" width="140">
                <template slot-scope="scope">{{scope.row.list[0].lockStock}}</template>
              </el-table-column>
            </el-table>
          </div>
          <!-- 多规格商品 -->
          <div v-if="commodityList[0].skuType == 1">
            <el-card shadow="always">
              <div class="flex-row-start">
                <div>{{commodityList[0].productName}}</div>
              </div>
              <el-table :data="commodityList[0].list" tooltip-effect="dark" style="width: 100%">
                <el-table-column label="规格" prop="skuName"></el-table-column>
                <el-table-column label="售价（元）" prop="price" width="140">
                  <template slot-scope="scope">{{ scope.row.price ? '￥'+scope.row.price : '-' }}</template>
                </el-table-column>
                <el-table-column label="成本价（元）" width="140">
                  <template slot-scope="scope">{{ scope.row.costPrice ? '￥'+scope.row.costPrice : '-' }}</template>
                </el-table-column>
                <el-table-column width="180">
                  <template slot="header" slot-scope="scope">
                    <span :data-scope="scope">砍价底价（元）</span>
                    <el-tooltip class="item" effect="dark" content="售价 > 底价 >= 0" placement="top">
                      <i class="el-icon-question"></i>
                    </el-tooltip>
                  </template>
                  <template slot-scope="scope">￥{{scope.row.finallyPrice}}</template>
                </el-table-column>
                <el-table-column label="库存" prop="skuStock" width="140"></el-table-column>
                <el-table-column label="活动库存" prop="lockStock" width="140"></el-table-column>
              </el-table>
            </el-card>
          </div>
        </el-form-item>
        <el-form-item label="帮砍金额：">
          <div>{{form.calculateType == 1 ? '随机金额' : '固定金额'}}</div>
        </el-form-item>
        <el-form-item label="帮砍人数：">
          <div>{{form.helpCount}}</div>
        </el-form-item>
        <el-form-item label="展示人数：">
          <div>{{form.isShow == 0 ? '展示' : '不展示'}}</div>
        </el-form-item>
        <el-form-item label="活动时间：">
          <el-date-picker
            disabled
            v-model="form.time"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="活动有效期：">
          <div>{{form.activityValidity ? form.activityValidity : '-'}}</div>
        </el-form-item>
        <el-form-item label="限购数量：">
          <div>{{form.limitNumber}}</div>
        </el-form-item>
        <el-form-item label="分享朋友圈标题：">
          <div>{{form.wxMomentsTitle ? form.wxMomentsTitle : '-'}}</div>
        </el-form-item>
        <el-form-item label="分享好友标题：">
          <div>{{form.wxFriendsTitle ? form.wxFriendsTitle : '-'}}</div>
        </el-form-item>
        <el-form-item label="分享好友文案：">
          <div>{{form.wxFriendsContent ? form.wxFriendsContent : '-'}}</div>
        </el-form-item>
      </el-form>
    </el-card>
    <div class="flex-row-center" style="margin-top:20px;">
      <el-button @click="back">返 回</el-button>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopActivityById } from '@/api/marketManage/discount'
export default {
  data(){
    return{
      form:{},
      commodityList:[]
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      getShopActivityById({id:this.$route.query.id}).then(res=>{
        console.log('活动详情',res)
        if(res.code == 200){
          this.form = res.data;
          this.form.time = [res.data.beginActivityTime,res.data.endActivityTime]
          this.commodityList = res.data.productList
        }else{
          this.$message.error(res.message);
        }
      })
    },
    back(){
      this.$router.replace({
        path:'/marketManage/bargain'
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .pageContent /deep/ .el-range-separator{
    line-height: 24px;
  }
</style>